body {
  margin: 0px;
  font-family: sans-serif; }

#animator1,
#animator2 {
  display: inline-block;
  width: 50%;
  margin-top: 80px;
  padding: 20px;
  box-sizing: border-box; }
  #animator1 .title,
  #animator1 .time,
  #animator2 .title,
  #animator2 .time {
    display: inline-block;
    width: 100%;
    text-align: center; }
  #animator1 .title,
  #animator2 .title {
    font-size: 28px; }
  #animator1 .time,
  #animator2 .time {
    font-size: 21px; }
  #animator1 .animation,
  #animator2 .animation {
    width: 100%;
    height: 50vh;
    margin: 20px 0px;
    overflow: hidden;
    box-shadow: 0px 0px 2px 0px #5f5f5f; }
    #animator1 .animation span,
    #animator2 .animation span {
      display: inline-block;
      position: relative;
      top: 5px;
      border-radius: 50%;
      border: 20px solid red;
      margin-left: -40px; }
  #animator1 .settings .field,
  #animator2 .settings .field {
    height: 40px;
    font-size: 18px; }
    #animator1 .settings .field input,
    #animator2 .settings .field input {
      float: right;
      font-size: 18px;
      width: 60px;
      margin: 0px;
      text-align: right; }
      #animator1 .settings .field input[type="checkbox"],
      #animator2 .settings .field input[type="checkbox"] {
        width: 20px; }

#controller {
  position: fixed;
  top: 0px;
  width: 100%;
  height: 80px;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  background-color: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 5px; }
  #controller button {
    vertical-align: top;
    outline-style: none;
    font-size: 21px;
    width: 21%;
    padding: 10px 0px;
    margin: 0px 2%;
    border-radius: 10px; }
    #controller button#start {
      margin-top: 0px;
      background-color: #00e600;
      box-shadow: 0px 4px 0px 0px green;
      border-width: 0px; }
      #controller button#start:active {
        margin-top: 3px;
        box-shadow: 0px 1px 0px 0px green; }
    #controller button#pause {
      margin-top: 0px;
      background-color: #ffc966;
      box-shadow: 0px 4px 0px 0px orange;
      border-width: 0px; }
      #controller button#pause:active {
        margin-top: 3px;
        box-shadow: 0px 1px 0px 0px orange; }
    #controller button#resume {
      margin-top: 0px;
      background-color: #6666ff;
      box-shadow: 0px 4px 0px 0px blue;
      border-width: 0px; }
      #controller button#resume:active {
        margin-top: 3px;
        box-shadow: 0px 1px 0px 0px blue; }
    #controller button#stop {
      margin-top: 0px;
      background-color: #ff6666;
      box-shadow: 0px 4px 0px 0px red;
      border-width: 0px; }
      #controller button#stop:active {
        margin-top: 3px;
        box-shadow: 0px 1px 0px 0px red; }
    #controller button[disabled] {
      opacity: 0.5; }
